<template>
  <el-form
    class="ling-base-info"
    label-position="right"
    label-width="120px"
    :model="getSettingInfo">
    <el-form-item label="设备号：">
      <el-input
        :readonly="true"
        v-model="getSettingInfo.device_no">
      </el-input>
    </el-form-item>

    <el-form-item label="程序版本号：">
      <el-input
        :readonly="true"
        v-model="getSettingInfo.version_name">
      </el-input>
    </el-form-item>
  </el-form>
</template>

<script>
import getSettingInfo from '@/mixins/getSettingInfo'
export default {
  name: 'LingBaseInfo',
  mixins: [getSettingInfo]
}
</script>

<style lang="scss">
.ling-base-info {
  .el-form-item {
    .el-input__inner {
      border-width: 0 0 1px 0;
      border-radius: 10px;
      font: {
        size: 16px;
        weight: bold;
      }
    }
  }
}
</style>



